/* General Team Section */
.team-section {
    background: linear-gradient(135deg, #e5e6f0, #ffffff);
    padding: 4rem 2rem;
    text-align: center;
}
.teamsection {
    background: linear-gradient(135deg, #e5e6f0, #ffffff);
    padding: 4rem 2rem;
    min-height: 100vh;
    text-align: center;
}

/* Title */
.team-title {
    font-size: 2.8rem;
    font-weight: bold;
    color: #051330;
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    letter-spacing: 1px;
}

/* Team Grid */
.team-grid {
    display: flex;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    flex-wrap: wrap;
}

/* Team Card - Glassmorphism Effect */
.team-card {
    position: relative;
    background: rgba(255, 255, 255, 0.84);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    padding: 1.8rem;
    border-radius: 1.2rem;
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
    width: 260px;
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    overflow: hidden;
}

.team-card:hover {
    transform: translateY(-12px);
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.15);
}

/* Profile Image */
.team-image {
    width: 110px;
    height: 110px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 1rem;
    border: 3px solid #ffffff;
    transition: transform 0.3s ease;
}

.team-card:hover .team-image {
    transform: scale(1.05);
}

/* Name & Role */
.team-name {
    font-size: 1.3rem;
    font-weight: 700;
    color: #051330;
    transition: color 0.3s ease;
}

.team-role {
    color: #6b7280;
    font-size: 1rem;
    font-weight: 500;
}

/* Overlay Effect */
.team-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    border-radius: 1rem;
}

.team-card:hover .team-overlay {
    opacity: 1;
}

/* LinkedIn Icon - Neumorphic Button */
.linkedin-icon {
    color: #0A66C2;
    font-size: 2rem;
    background: white;
    padding: 0.6rem;
    border-radius: 50%;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1),
                -4px -4px 8px rgba(255, 255, 255, 0.7);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.linkedin-icon:hover {
    transform: scale(1.1);
    box-shadow: 6px 6px 12px rgba(0, 0, 0, 0.15),
                -6px -6px 12px rgba(255, 255, 255, 0.9);
}

/* Responsive Styles */
@media (max-width: 768px) {
    .team-title {
        font-size: 2.2rem;
    }

    .team-card {
        width: 220px;
        padding: 1.5rem;
    }

    .team-image {
        width: 90px;
        height: 90px;
    }
}

@media (max-width: 480px) {
    .team-title {
        font-size: 2rem;
    }

    .team-card {
        width: 200px;
        padding: 1.2rem;
    }

    .team-image {
        width: 80px;
