/* Footer.css */

/* Footer Container */
.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  width: 100%;
  box-sizing: border-box;
  flex-wrap: wrap; /* Allow footer sections to wrap on smaller screens */
}

/* Big Footer Section - First Section */
.big-footer {
  flex: 2 1;
  height: 155px;
  background: #f5f5f5;
  border-top: 4px solid #CFD0E1;
  border-bottom: 4px solid #CFD0E1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
  flex-wrap: wrap; /* Allow content to wrap */
}

.footer-contact {
  text-align: left;
}

.footer-title {
  color: #545B5F;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.footer-email {
  color: #545B5F;
  font-family: Inter, sans-serif;
  font-size: 25px;
  font-weight: 800;
  margin-top: 10px;
}

.footer-socials {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
}

.social-icon {
  width: 40px;
  height: 40px;
  cursor: pointer;
}

/* Small Footer Sections - Rest of the Sections */
.small-footer {
  flex: 1 1;
  height: 155px;
  background: #f5f5f5;
  border: 4px solid #CFD0E1;
  border-left: 2px solid #CFD0E1;
  border-right: 2px solid #CFD0E1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  box-sizing: border-box;
}

.footer-link {
  color: #545B5F;
  font-family: Inter, sans-serif;
  font-size: 15px;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-link:hover {
  color: #000;
}

/* Bottom Footer Bar */
.bottom-footer {
  width: 100%;
  height: 50px;
  background-color: #f5f5f5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  box-sizing: border-box;
}

.bottom-footer-text {
  font-family: Inter, sans-serif;
  font-size: 14px;
  color: #333;
  font-weight: 500;
}

/* Social Icons Section - Separate Divs */
.footer-socials {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
}

.icon-wrapper {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.icon-wrapper::before {
  content: '';
  position: absolute;
  top: -100%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #CFD0E1; /* Hover fill color */
  transition: top 0.3s ease-in-out;
  z-index: -1;
}

.icon-wrapper:hover::before {
  top: 0; /* Fill from top to bottom */
}

.icon-wrapper:hover {
  transform: translateY(-5px); /* Slight lift on hover */
}

.social-icon {
  width: 30px;
  height: 30px;
  z-index: 1; /* Keep icons above the hover effect */
}

.footer-email  {
  text-decoration: none;
}

a{
  text-decoration: none;
}

